body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: black;
}

body {
  max-width: 768px !important;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home-banner {
  background-image: url(/lcx-project/static/media/banner.3e987c0b.jpg);
  height: 300px;
  width: 100%;
  margin-top: -10px;
}

.banner-section {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.banner-text {
  color: white;
  text-align: center;
}

.tab-container {
  position: -webkit-sticky !important;
  position: sticky !important;
}

.header {
  height: 90px;
}

.header-container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.section-pages {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.link-text {
  text-decoration: none !important;
  color: black;
}

.intro-container {
  padding: 5% 10%;
  text-align: center;
}

.card-container {
  padding: 6% 10% 1% 10%;
}

.tab-container {
  padding: 0 10%;
  box-shadow: rgba(126, 142, 177, 0.12) 0px 2px 4px;
  max-width: 1200px;
  margin: auto;
  min-height: 630px;
}

.vault-card {
  text-align: center;
}

.footer-section {
  background: #2b2b2b;
  color: white;
}
.footer1 {
  margin: auto;
  margin-top: 10%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 5% 15%;
}

.contact-text {
  font-size: 1.1rem;
}
.footer-bottom {
  max-width: 1200px;
  margin: auto;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.link-text1 :hover {
  color: black !important;
}

.link-text1 {
  text-decoration: none !important;
  color: black;
}

.advisor-image {
